.ult-modal-options{
	.uo_certificates_clear_btn,
	.uo_certificates_cleanup_btn{
		width: 350px;
	}
	.uo_certificates_clear_btn:hover,
	.uo_certificates_cleanup_btn:hover{
		color: #ffffff;
	}
	#uo-cert-in-bulk-clear-success,
	#uo-download-cert-in-bulk-success{
		color: green;
		display: none;
	}
	#uo-cert-in-bulk-clear-warning,
	#uo-download-cert-in-bulk-warning{
		color: red;
		display: none;
	}
	.ult-modal-form-row__field input[name="uncanny-group-certificate-in-bulk-minimum-storage"]{
		width: 100%;
	}
}
